/* CommonCardStyles.css */

.card {
  width: 360px; /* Relative width to the parent container */
  height: 70px; /* Let height adjust based on content */
  border: 1px solid #D0D0D0;
  border-radius: 4px;
  background-color: #FFFFFF;
  padding: 10px; /* Adjusted padding */
  position: relative;
  margin: 5px 0;
  box-sizing: border-box; /* Include padding and border in element's total width and height */
}

.label, .value, .location, .distance {
  font-family: 'Times New Roman', serif;
}

.label {
  font-size: 1rem; /* Responsive font size */
  color: #000;
  position: absolute;
  top: 10px;
  left: 15px;
}

.value {
  font-size: 1rem; /* Responsive font size */
  font-weight: bold;
  color: #000;
  position: absolute;
  bottom: 10px;
  left: 15px;
}

.location {
  font-size: 1.25rem; /* Responsive font size */
  font-weight: bold;
  color: #000;
  position: absolute;
  bottom: 10px;
  left: 15px;
}

.distance {
  font-size: 0.75rem; /* Responsive font size */
  color: #666;
  position: absolute;
  top: 10px;
  right: 15px;
}

.refresh-indicator {
  position: absolute;
  top:50px; /* Position it at the top right of the card */
  right: 10px; /* Position it at the top right of the card */
  width: 10px; /* Size of the dot */
  height: 10px; /* Size of the dot */
  border-radius: 50%; /* Circular dot */
  background-color: rgb(0, 0, 0); /* Default color when not refreshing */
  transition: background-color 0.3s; /* Smooth transition */
}

.refresh-indicator.active {
  background-color: rgba(128, 128, 128, 0); /* Color when refreshing */
}
